/* Styles for auto-generated docs */

$mH: 24px;

body, html {
    background-color: white;
    color: #333;
}

body {
    font-size: 90%;
    margin: 50px 15%;
}

h1, h2, h3 {
    margin-top: $mH;
    padding-top: $mH;
    text-transform: uppercase;
}

h1 {
    border-top: 1px solid #ccc;
}

img {
    max-width: 100%;
}

th, td {
    font-size: 0.8rem;
    padding-right: 10px;
}

th {
    text-align: left;
}

code {
    background-color: #eee;
    border-radius: 2px;
    display: inline-block;
    padding: 1px 5px;
}